  <div class="row">
    <div class="col" style="padding:30px 10px;">
      <div class="row">
        <button class="btn-sq" @click="keyboardActions.up">
          <svg height="20" width="20" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512">
            <path
              fill="currentColor"
              d="M177 159.7l136 136c9.4 9.4 9.4 24.6 0 33.9l-22.6 22.6c-9.4 9.4-24.6 9.4-33.9 0L160
          255.9l-96.4 96.4c-9.4 9.4-24.6 9.4-33.9 0L7 329.7c-9.4-9.4-9.4-24.6
          0-33.9l136-136c9.4-9.5 24.6-9.5 34-.1z"></path>
          </svg>
        </button>
      </div>
      <div class="row">
        <button class="btn-sq" @click="keyboardActions.left">
          <svg height="20" width="20" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512">
            <path
              fill="currentColor"
              d="M31.7 239l136-136c9.4-9.4 24.6-9.4 33.9 0l22.6 22.6c9.4 9.4 9.4 24.6 0
              33.9L127.9 256l96.4 96.4c9.4 9.4 9.4 24.6 0 33.9L201.7 409c-9.4 9.4-24.6 9.4-33.9
              0l-136-136c-9.5-9.4-9.5-24.6-.1-34z"></path>
          </svg>
        </button>
        <button class="btn-sq" @click="keyboardActions.down">
          <svg height="20" width="20" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512">
            <path
              fill="currentColor"
              d="M143 352.3L7 216.3c-9.4-9.4-9.4-24.6 0-33.9l22.6-22.6c9.4-9.4 24.6-9.4 33.9
              0l96.4 96.4 96.4-96.4c9.4-9.4 24.6-9.4 33.9 0l22.6 22.6c9.4 9.4 9.4 24.6 0 33.9l-136
              136c-9.2 9.4-24.4 9.4-33.8 0z"></path>
          </svg>
        </button>
        <button class="btn-sq" @click="keyboardActions.right">
          <svg height="20" width="20" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512">
            <path
              fill="currentColor"
              d="M224.3 273l-136 136c-9.4 9.4-24.6 9.4-33.9 0l-22.6-22.6c-9.4-9.4-9.4-24.6
              0-33.9l96.4-96.4-96.4-96.4c-9.4-9.4-9.4-24.6 0-33.9L54.3 103c9.4-9.4 24.6-9.4 33.9
